import React, { Component, Fragment } from 'react';
import MessagesControl from './MessagesControl';
import Checkbox from '../checkbox/Checkbox';
import NavigationOption from '../navigationOption/NavigationOption';

class MessagesControlDocs extends Component {
  constructor(props) {
    super(props);
    this.state = {
      helpLink: 'https://reactjs.org',
    };
  }

  render() {
    const { helpLink } = this.state;

    return (
      <div className="container">
        <section className="section">
          <div className="row">
            <div className="col-md-6">
              <h2>MessageControl</h2>
              <p>Messaging for form controls</p>
            </div>
            <div className="col-md-6">
              <MessagesControl
                type="warning"
                content="Lorem ipsum dolor sit amet, consectetur adipisicing elit. Libero, maiores."
              >
                <input type="text" placeholder="Username" className="form-control" />
              </MessagesControl>
            </div>
          </div>
          <div className="row">
            <div className="col-md-6">
              <p>
                <code>MessageControl</code> with simple text input.
              </p>
              {/* eslint-disable react/jsx-indent */}
              <pre className="tw-docs-code">
                {`<MessagesControl
    type="warning"
    content={'Lorem ipsum dolor sit amet, consectetur adipisicing elit. Libero, maiores.'}>
    <input type="text" placeholder="Username" className="form-control" />
</MessagesControl>`}
              </pre>
            </div>
          </div>
        </section>

        <section className="section">
          <div className="row">
            <div className="col-md-6">
              <MessagesControl type="error" content="Required">
                <Checkbox label="OK?" onChange={null} />
              </MessagesControl>
            </div>
          </div>
          <div className="row">
            <div className="col-md-6">
              <p>
                <code>MessageControl</code> with simple text input.
              </p>
              {/* eslint-disable react/jsx-indent */}
              <pre className="tw-docs-code">
                {`<MessagesControl
    type="error"
    content="Required">
    <Checkbox label="OK?" onChange={...} />
</MessagesControl>`}
              </pre>
            </div>
          </div>
        </section>

        <section className="section">
          <div className="row">
            <div className="col-md-6">
              <MessagesControl
                type="info"
                content={
                  <Fragment>
                    Check more info about this <a href={helpLink}>here</a>.
                  </Fragment>
                }
              >
                <NavigationOption
                  id="navigation-option"
                  name="navigation-option"
                  title="Navigation option"
                  description="Normally, the button and icon are vertically centered."
                  onClick={null}
                  media={<cursor />}
                />
              </MessagesControl>
            </div>
          </div>
          <div className="row">
            <div className="col-md-6">
              <p>
                <code>MessageControl</code> with simple text input.
              </p>
              {/* eslint-disable react/jsx-indent */}
              <pre className="tw-docs-code">
                {`<MessagesControl
    type="info"
    content={<Fragment>Check more info about this <a href={helpLink}>here</a>.</Fragment>}>
    <NavigationOption
                                id="navigation-option"
                                name="navigation-option"
                                title="Navigation option"
                                description="Normally, the button and icon are vertically centered."
                                onClick={null}
                                media={<cursor />}
                              />
</MessagesControl>`}
              </pre>
            </div>
          </div>
        </section>
      </div>
    );
  }
}

export default MessagesControlDocs;



